<script>
import loadXMLDoc from '@/store/AjaxRequest.js';

export default {
    data() {
        return {
            //活动名称
            ActivityName: "",

            // 活动类型
            ActivitySelected: "",
            ActivitySelectedArr: [],

            // 活动状态
            ActivityState: '111',

            // 开始时间
            setActivity: "",
            cwsetActivity: "",

            // 结束时间
            overActivity: "",
            cwoverActivity: "",

            // 最大人数
            allPeople: '',
            cwallPeople: '',

            //活动描述
            inputText: "",

            // 搜索活动
            ActivitySearch: "",

            // // 创建按钮禁用
            // isDisabledcj: false
        }
    },

    methods: {
        // 发布活动
        setActive() {
            if (!this.ActivitySelected) {
                return
            }
            const data = this.ActivitySelectedArr.filter(item => item.activity_type_config == this.ActivitySelected)[0].config_id
            loadXMLDoc({
                reqBody: {
                    'token': localStorage.getItem("token"),
                    'deviceid': localStorage.getItem("equipment"),
                    "activity_name": this.ActivityName,
                    "activity_type": data,
                    "activity_startTime": this.setActivity,
                    "activity_finishTime": this.overActivity,
                    "activity_peopleCounting": this.allPeople,
                    "activity_text": this.inputText,
                },
                reqUrl: "http://8.148.31.153:3030/setActivity",
                callback: (error, resdata) => {
                    try {
                        if (!error) {
                            const data = JSON.parse(resdata)
                            if (data.success) {
                                this.$store.commit("tanchaung", "活动创建完成")
                                this.$store.commit("tanchuangshow", true)
                                this.ActivityName = ""
                                this.ActivitySelected = ""
                                this.setActivity = ""
                                this.overActivity = ""
                                this.allPeople = ""
                                this.inputText = ""
                            } else {
                                this.$store.commit("tanchaung", "活动创建失败")
                                this.$store.commit("tanchuangshow", true)
                            }
                        }
                    } catch (error) {
                        console.log(error)
                    }
                }
            })
        },

        //开始时间错误 
        srsetActivity() {
            if (/^20[2-9][0-9]-(0[1-9]|1[0-2])-(0[1-9]|[12][0-9]|3[01]) (0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]$/.test(this.setActivity)) {
                this.cwsetActivity = ''
            } else {
                this.cwsetActivity = "格式错误( 示例 2025-07-14 15:03 )"
            }
        },

        // 结束时间错误
        sroverActivity() {
            if (/^20[2-9][0-9]-(0[1-9]|1[0-2])-(0[1-9]|[12][0-9]|3[01]) (0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]$/.test(this.overActivity)) {
                this.cwoverActivity = ''
            } else {
                this.cwoverActivity = "格式错误( 示例 2025-07-14 15:03 )"
            }
        },

        // 人数错误
        srallPeople() {
            if (/^\d*$/.test(this.allPeople)) {
                this.cwallPeople = ''
            } else {
                this.cwallPeople = "请输入正确人数"
            }
        },
    },

    created() {
        // 活动类型
        const value = localStorage.getItem("getConfig")
        if (value == "null" || value == "undefined" || value == '') {
            // 本地存储没数据
            loadXMLDoc({
                reqBody: { 'token': localStorage.getItem("token"), 'deviceid': localStorage.getItem("equipment") },
                reqUrl: "http://8.148.31.153:3030/getConfig",
                callback: (error, resdata) => {
                    try {
                        if (!error) {
                            const data = JSON.parse(resdata)
                            this.ActivitySelectedArr = data.resData
                            localStorage.setItem("getConfig", resdata)
                        }
                    } catch (erroe) {
                        console.log(erroe)
                    }
                }
            })
        } else {
            // 本地存储有数据
            const data = JSON.parse(localStorage.getItem("getConfig"))
            this.ActivitySelectedArr = data.resData
        }

    }
};
</script>

